#RegistrationPageDiv {
  background-color: #d0d0d0;
  display: grid;
  grid-template-rows: 1fr 6fr 1fr;
  grid-template-columns: 1fr 1fr 1fr;
  height: 100vh;
  width: 100vw;
}

@media screen and (min-width: 1500px) {
  #RegistrationPageDiv {
    grid-template-columns: auto 500px auto;
  }
}

#LoginLogoBanner {
  color: #333;
  background-color: #f6f6f6;
  grid-row: 1 / 2;
  grid-column: 2 / 3;
  height: fit-content;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
}

#RegistrationPageSignInFormDiv {
  background-color: #f6f6f6;
  grid-row: 2 / 3;
  grid-column: 2 / 3;
  margin-top: 1.15em;
  border-radius: 10px;
  box-shadow: 4px 4px 10px rgb(90, 90, 90);
  height: fit-content;
}

#RegistrationPageSignInFormDiv h2 {
  margin: 0;
  text-align: center;
  background-color: #b90504;
  cursor: pointer;
  color: #f6f6f6;
  border: none;
  font-size: 18pt;
  padding: 0.35em;
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
}

#SignInForm {
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: 1fr 1fr 1fr;
  padding: 2em;
  gap: 2em;
}

#SignInForm input {
  font-size: 16pt;
  color: #333;
  background-color: #f6f6f6;
  border: none;
  border-bottom: 3pt solid #b90504;
}

#SignInForm input::placeholder {
  color: #333;
}

#SignInForm input:focus-visible {
  outline: none;
}
